body {
  background: url(http://kshisa.ru/images/butt/gorinoch.png) no-repeat;
  background-size: cover;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}
#cent {
    font-family: "Sans MS", Verdana, Arial, Helvetica, sans-serif;
    text-align: center;
    color: #675353;
    font-weight: 600;
    font-style: oblique;
    margin-left: auto;
    margin-right: auto;
    height: 600px;
}
.image {
    height: 122px;
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
}
#posters {
    height: 140px;
}
.Rating { 
    font-size: 20px; 
}
.head {
    height: 60px;
}
